@import url(https://fonts.googleapis.com/css?family=Changa+One);
html {
  width: 100vw;
  height: 100vh;
}
body {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
#root {
  height: 100%;
  width: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  box-sizing: border-box;
}
.app {
  width: 100%;
  height: 100%;
	margin: 0;
  font-family: 'Roboto Condensed', sans-serif;
  scroll-behavior: smooth;
}
    .grid {
      width: 100%;
      height: 100%;
      display: grid;
      grid-template-rows: repeat(3, 210px 60px auto);
      /* grid-template-areas:  */
     
    } 



.header {
  display: grid;
  grid-template-areas: 
  "logo title title title title title items";
/*   
  background-color: lightgrey; */
  max-height: 110px;
}
  .logo--logo {
    grid-area: logo;
  }
  .title--title {
    margin-top: 20px;
    grid-area: title;
    align-self: auto;
  }
  .header-items {
    grid-area: items;
    display: grid;
    grid-template-areas: 
    "flag login"
    "flag login"
    "flag login"
    "flag login";
    max-height: 40px;
  }
    .flag {
      grid-area: flag;
      align-self: center;
      cursor: pointer;
    }
    .Login {
      grid-area: login;
      margin: 10px 10px 10px 0;
      padding-right: 10px;
      text-decoration: underline;
      color: darkblue;
      cursor: pointer;
    }

  
  @media only screen and (max-width: 420px) {
      .header{
        display: grid;
        grid-template-areas: 
        "title title title items"
        "title title title items";
        height: 80px;
      }

      .logo--logo {
        display: none;
      }
    }
.title {
  font-family: 'Changa One';
  font-size: 75px;
  padding-left: 230px;
  align-self: center;
  /* text-align: center; */
  color: darkblue;
}

@media only screen and (max-width: 420px) {
  .title{
    align-self: center;
    font-size: 40px;
    padding-left: 30px;
  }
}
.logo {
  margin: 30px
}

/* @media only screen and (max-width: 420px) {
  
  .logo {
    display: none;
  }
} */
.login--form {
  display: none;
}
.menu {
  margin: 30px 15px 0 15px;
  
}
  .hamburger {
    display: none;
  }
  .menu--dropdown {
    justify-content: space-evenly;
    display: grid;
    grid-template-columns: repeat(5, auto);
    position: sticky;
    position: -webkit-sticky;
    top: -1px;
    background-color: #fff;
    z-index: 9999;
    -webkit-transition: height 2s;
    transition: height 2s;
  }

@media only screen and (max-width: 420px) {
  
  .hamburger {
    display: block;
    cursor: pointer;
  }
  
    .menu--dropdown {
      grid-template-columns: 100%;
      grid-template-rows: repeat(5, auto);
      padding: 20px;
      -webkit-transition: height 2s;
      transition: height 2s;

    }

    .menu--dropdown.open {
      height: 100%;
      width: 130px;
      visibility: visible;
      display: block;
      background-color: #f9f9f9;
      box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
      box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
      z-index: 1;
      -webkit-transition-timing-function: linear;
              transition-timing-function: linear;
      
    }
    .menu--dropdown.closed {
      height: 0px;
      display: none;  
      -webkit-transition-timing-function: linear;  
              transition-timing-function: linear;
    }

  
}
 

  
.menu--item {
  color: darkblue;
  font-size: 20px;
  align-self: end;
  display: grid;
  grid-template-columns: 35px auto;

}
.menu--item:hover {
    text-decoration: underline;
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    cursor: pointer;
    font-weight: 550;
  }

  .icon {
    align-self: start;
    justify-self: right;
    margin-right: 4px;
  }
  .name {
    align-self: center;
  }

  @media only screen and (max-width: 420px) {
    .icon {
      display: none;
    }
    .menu--item {
      font-size: 18px;
      margin: 1px 0 4px 0;
      
    }
    .menu--item:hover {
      color: white;
      text-decoration: none;
      -webkit-transform: none;
              transform: none;
      -webkit-transform: initial;
              transform: initial;
      cursor: pointer;
      background-color: grey;
    }
    .name {
      margin: 1px solid black;
    }
  }
.icon--home {
  margin-left: 150px;
}
.content--grid {
  display: grid;
  width: 100%;
  height: 100vh;
  grid-template-areas: 
  'left main main main right';
  /* grid-template-columns: 300px auto 300px;
  grid-template-rows: 100vh; */
  overflow-y: scroll;
  
}
  .content--left,
  .content--main,
  .content--right {
    height: 100vh;
  }
  .content--left {
    grid-area: left;
  }
    .news {
      box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
      box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
      margin: 2em 1.7em 2em 1.7em;
      width: 270px;
      height: 40vh;
      font-weight: 550;
      
    }
    h3 {
      padding-top: 30px;
    }
    h3, h4 {
      margin: 12px;
      text-align: justify;
      line-height: 20px;

    }
    h4 {
      font-weight: normal;
    }
    .content--main {
      box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
      box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
      margin: 2rem;
      grid-area: main;
      
    }
      h1 {
        padding: 0 0 10px 20px;
      }
      p {
        padding: 0 20px;
        text-align: justify;
        line-height: 26px;

        
      }
    
      .content--right {
        display: grid;
        grid-template-rows: 35vh 5vh 5vh;
        grid-area: right;
        
      } 
        .videos {
          margin-top: 35px;
          
        }
        
        .canal-youtube--text {
          margin-top: 15px;
          max-height: 50px;
        }
        .canal-youtube {
          display: grid;
          grid-template-columns: 60px 220px;
          max-height: 50px;
          align-self: top;
        }



@media only screen and (max-width: 420px) {
  .content--grid {
    display: grid;
    grid-template-areas: 
    'main'
    'main'
    'left'
    'right';
  }
  .content--left,
  .content--right{
    height: 50vh;
  }

  .content--main{
    height: 100vh;
  }
  .content--right {
    justify-content: center;
    display: inline;
    justify-self: center
  }
  .news {
    width: 85%;
  }
  
}
.icon--youtube {
  margin-right: 10px;
}
.icon--youtube:hover {
  cursor: pointer;
}
.card {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr) );
  grid-gap: 50px;
  grid-template-rows: repeat(3, auto);
  margin: 30px 15px 30px 0; 
  padding: 25px;
  max-width: 100%;
  justify-content: space-around;
}


  .card--grid {
    display: grid;
    grid-template-rows: 30px auto 50px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    
    /* height: 50vh; */
    width: 100%;
    margin: 10px;
  }

    
    .card--text {
      justify-items: center;
    }

@media only screen and (max-width: 420px) {
  .card {
    display: grid;
    grid-template-columns: auto;
    grid-template-rows: repeat(12, auto)

  }
    .card--grid {
      box-shadow: none;
      border: 1px solid lightgrey;
    }
    

}

.posts {
  display: grid;
  grid-template-areas: 
  "old new new . . ."
  "old new new . . ." 
}

  .posts--old {
    grid-area: old;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    margin: 2em 2em 2em 1.7em;
    max-width: 270px;
    height: 40vh;
    font-weight: 550;

  }
  .posts--new {
    grid-area: new;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    margin: 2em 10em 2em 2em;
    max-width: 100%;
    height: 100vh;
  }
    h1,
    p {
      margin: 20px 30px;
    }

    h3, h4 {
      margin: 0 20px;
      text-align: justify;
      line-height: 20px;

    }
    h4 {
      font-weight: normal;
      padding-top: 25px;
      cursor: pointer;
    }

    h4:hover {
      text-decoration: underline;
      color: darkblue;
    }


@media only screen and (max-width: 420px) {
  .posts {
    display: grid;
    grid-template-areas: 
    'new new new'
    'new new new'
    'new new new'
    'old old old';
  }
}
